@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  color: white;
  font-family: "Inter", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
}

.conteiner {
  background-color: #22252c;
  max-width: 420px;
  border-radius: 10px;
  box-shadow: 0 0 15px rgb(26, 26, 26);
}

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 30s linear;
            animation: App-logo-spin infinite 30s linear;
  }
}
.ProfilPhoto {
  height: 40vh;
  width: 100%;
  box-shadow: 0 5px 5px rgb(32, 32, 32);
  border-radius: 10px 10px 0 0;
  background-image: url(../../components/images/20220405_111318.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

h1 {
  min-height: 50px; /* 100vh */
  font-size: 3.5em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.headings {
  margin-top: 30px;
}

p {
  padding: 0 20px 40px 20px;
  max-width: 500px;
  text-align: start;
  display: flex;
  justify-content: center;
  align-items: center;
}

h2 {
  text-align: start;
  padding: 0 30px;
}

h1,
h3,
h4 {
  margin: 0;
  min-height: inherit;
  padding: 0;
}

h3 {
  color: rgb(121, 121, 121);
}

h4 {
  color: orange;
}

.linkedin {
  background-color: #1f9ad3;
  color: white;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 0;
}
.buttons button {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  margin: 0 20px;
  padding: 10px 40px;
  border-radius: 10px;
  border: none;
  box-shadow: 0 0 5px black;
}
.buttons button img {
  padding-right: 10px;
}

footer {
  padding: 20px;
  background-color: #191b20;
  border-radius: 0 0 10px 10px;
}

a {
  padding: 0 20px;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media only screen and (max-width: 420px) {
  .ProfilPhoto {
    border-radius: 0;
  }
  footer {
    border-radius: 0;
  }
}/*# sourceMappingURL=App.css.map */